}
.texttitle {
  background: #fa666633;
  width: 30vw;
  height: 10vh;
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
}
.mainbutton1 {
  position: absolute;
  width: 30%;
  height: 50%;
  font-weight: bold;
