  top: 85%;
  left: 60%;
}

.city {
  background-color: #8cd3e4;
  overflow: hidden;
  width: 80%;
  height: 80%;
}

.holder {
  //@include align(both);
  position: absolute;
  width: 80%;
  height: 80%;
  transform-style: preserve-3d;
  transform: perspective(1200) rotate3d(1, 0, 0, 0);
  transition: transform 1s ease;
  perspective: 500px;
  overflow: visible;

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 150%;
    height: 150%;
    overflow: visible;
    display: block;
    transform: translate(-50%, -50%);
  }

  g {
    overflow: visible;
  }

  .rotate {
    transform-origin: left;
    transition: transform 1s ease;
    transform-origin: bottom;
    padding-top: 50px;
    position: absolute;
  }

  &:hover {
    transform: perspective(1200) rotate3d(0, 1, 0, -20deg) translateX(-50px);

    .rotate {
      @for $i from 1 to 20 {
        &:nth-of-type(#{$i}) {
          transform: rotateY(12deg) translateX(#{-15px * $i});
        }
      }
    }
  }
}
.waves {
  position: absolute;
  width: 140%;
  height: 50vh;
  margin-bottom: -7px; /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
  top: 470px;
  left: -100px;
}
.parallax > use {
  animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.parallax > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.parallax > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}

@keyframes walkanim {
  to {
    background-position: 100% 0;
  }
}
@keyframes stroll {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(100%);
  }
}
#walk-container {
  display: inline-block;
  position: absolute;
  width: 33%;
  top: 10%;
  padding-bottom: 150%;
  vertical-align: middle;
  overflow: hidden;
  animation: stroll 10s linear infinite;
}
#walk {
  display: inline-block;
  position: absolute;
  top: 45%;
  left: -15%;
